.backButton {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  padding: 0 !important;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 8px 0 0;
}

.title {
  font-size: 2rem !important;
  font-weight: bold;
  margin-bottom: 0;
}

.container {
  padding-top: 8px;
}

.logo {
  height: 48px;
  margin: 0 8px;
}

// INCIDENT

.incident-col {
  padding: 0;
}

.incident-item {

  .incident-img-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 75%;

      padding: 8px;

      border-radius: 50%;
      background-color: lightgray;
    }
  }

  .incident-desc {
    color: black;
    text-align: center;
    text-decoration: none;
    font-weight: bold;
    word-break: break-word;

    margin: 6px;
  }
}


.arrows {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0 8px 8px 8px;

  p {
    font-weight: bold;
  }
}

.custom-radio, .custom-checkbox {
  margin: 5px 0;
}
